import React, { useEffect, useState } from "react";
import "./newList.css";
import trashIcon from "./trashbin.png";
import downloadIcon from "./download.png";

const Year = (props) => {
  if (props.month === null) {
    props.y.months.sort((a, b) => (a > b ? true : false));
    return (
      <div>
        <ul className="container">
          {props.y.months.map((m) => (
            <li key={m.month} onClick={() => props.setMonth(m)}>
              <b>{m.month}</b>
              <div>Tage:{m.days.length}</div>
            </li>
          ))}
        </ul>
      </div>
    );
  } else {
    return <Month m={props.month} {...props} />;
  }
};
const Month = (props) => {
  if (props.day === null) {
    props.m.days.sort((a, b) =>
      new Number(a.day) > new Number(b.day)
        ? 1
        : new Number(a.day) < new Number(b.day)
        ? -1
        : 0
    );
    return (
      <div>
        <ul className="container">
          {props.m.days.map((d) => (
            <li key={d.day} onClick={() => props.setDay(d)}>
              <b>{d.day}</b>
              <div>Bilder:{d.files.length}</div>
            </li>
          ))}
        </ul>
      </div>
    );
  } else {
    return <Day d={props.day} {...props} />;
  }
};
const Day = (props) => {
  const [size, setSize] = useState(undefined);
  useEffect(() => {
    const size = () => {
      const s = window.innerWidth > 1000 ? 4 : window.innerWidth > 700 ? 2 : 1;
      if (s !== size) {
        setSize(s);
      }
      return s;
    };
    size();
    window.addEventListener("resize", () => {
      size();
    });
  }, []);
  const ar = [[]];
  let y = 1;
  for (let x = 0; x < props.d.files.length; x++) {
    if (x - size * y === 0) {
      y += 1;
      ar.push([]);
    }
    ar[y - 1].push(props.d.files[x]);
  }
  return (
    <div className="day-List container">
      {ar.map((_f) => (
        <div style={{ display: "flex", flexDirection: "row" }}>
          {_f.map((f) => {
            let filePath = props.IPS.picture;
            filePath += "/" + props.y.year;
            filePath += "/" + props.m.month;
            filePath += "/" + props.d.day;
            filePath += "/" + f;

            return (
              <div className="day-Container">
                <b>{f}</b>
                <div className="file-icons">
                  <img src={trashIcon} width={20} />
                  <img src={downloadIcon} width={20} />
                </div>
                <img src={filePath} alt="no Pic" width={250} />
              </div>
            );
          })}
        </div>
      ))}
    </div>
  );
};

const Container = (props) => {
  const year = props.year;
  const setYear = props.setYear;
  if (year === null) {
    return (
      <div>
        <ul className="container">
          {props.PictureFileStructure.map((y) => (
            <li key={y.year} onClick={() => setYear(y)}>
              <b>{y.year}</b>
              <div>Monate:{y.months.length}</div>
            </li>
          ))}
        </ul>
      </div>
    );
  } else {
    return <Year y={year} {...props} />;
  }
};

const FileStructure = (props) => {
  const [year, setYear] = useState(null);
  const [month, setMonth] = useState(null);
  const [day, setDay] = useState(null);

  console.log(props.PictureFileStructure);

  if (props.PictureFileStructure === undefined) return "loading";

  const back = () => {
    if (month) {
      if (day) {
        setDay(null);
      } else {
        setMonth(null);
      }
    } else {
      setYear(null);
    }
  };
  return (
    <div className="List-Container">
      <div className="List-header">
        {year ? <button onClick={back}>back</button> : null}
        <b>
          {year
            ? month
              ? day
                ? `${year.year}->${month.month}->${day.day}`
                : `${year.year}->${month.month}`
              : `${year.year}`
            : "Foto Auswählen"}
        </b>
      </div>
      <Container
        {...props}
        year={year}
        setYear={setYear}
        month={month}
        setMonth={setMonth}
        day={day}
        setDay={setDay}
      />
    </div>
  );
};
export default FileStructure;



