import React, { useEffect, useRef, useState } from "react";
export const Headline = (props) => {
  const [res, _setRes] = useState(1);
  const [rtime, _setrtime] = useState(5);
  const setRes = (value) => {
    _setRes(value);
    props.ws.send(JSON.stringify({ type: "change-res", data: { res: value } }));
  };
  const setrTime = (value) => {
    _setrtime(value);
    props.ws.send(
      JSON.stringify({ type: "change-time", data: { time: value } })
    );
  };
  return (
    <div
      style={{
        position: "relativ",
        display: "flex",
        flexDirection: "row",
        height: "130px",
      }}
    >
      <h2>SK4 KatzenWache</h2>
      <div style={{ position: "absolute", right: "20px" }}>
        <table>
          <tbody>
            <tr>
              <td>Auflösung</td>
              <td>
                <b>{res == 1 ? "niedrig" : res == 2 ? "mittel" : "hoch"}</b>
                <input
                  type="range"
                  max={3}
                  min={1}
                  defaultValue={1}
                  value={res}
                  onChange={(target) => setRes(new Number(target.target.value))}
                />
              </td>
            </tr>
            <tr>
              <td>Auslösezeit</td>
              <td>
                <b>{rtime.toString() + "s"}</b>
                <input
                  type="range"
                  min={5}
                  defaultValue={5}
                  max={20}
                  value={rtime}
                  step={5}
                  onChange={(target) =>
                    setrTime(new Number(target.target.value))
                  }
                />
              </td>
            </tr>
            <tr>
              <td>Foto/Video</td>

              <td>
                <input type="checkbox"></input>
              </td>
            </tr>
            <tr>
              <td>Spamschutz</td>
              <td>
                <input placeholder="0s"></input>
              </td>
            </tr>
            <tr>
              <td>Speicherplatz</td>
              <td>
                <input placeholder="100mb"></input>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};



